/* sets universal box-sizing to border-box (The width and height properties (and min/max properties) includes content, padding and border) */
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  
  /* the modal window needs to use all the window space*/
  min-height: 100vh;
}

/* Necesary for the no user selection redmine issue: #5242*/
body, button > span, .suppress-selection, .suppress-selection * {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}

.hide-selection::-moz-selection {
  background-color: transparent;
}

.hide-selection::selection {
  background-color: transparent;
}

.appContainer {
  position: relative;
}

.overflow-hidden {
  overflow: hidden;
}

/* loader */
.loader:empty {
  position: absolute;
  top: calc(50% - 4em);
  left: calc(50% - 4em);
  width: 6em;
  height: 6em;
  border: 1.1em solid rgba(0, 95, 172, 0.2);
  border-left: 1.1em solid rgb(0, 95, 172);
  border-radius: 50%;
  -webkit-animation: load8 1.1s infinite linear;
          animation: load8 1.1s infinite linear;
}

@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/***************************************
Input Style - Checkbox and radio buttons 
***************************************/
.inputWrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: left!important;
}

.inputWrapper input {
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto 0;
  left: 2.5px;
}

.inputWrapper .radioWrap {
  height: 100%;
}

.inputWrapper label > span {
  margin-left: 20px;
  display: block;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  height: 100%;

  /* disable text selection on RadioButton */
  -webkit-user-select: none;  
  -moz-user-select: none;    
  -ms-user-select: none;      
  user-select: none;
}

.inputWrapper label .inputTextWrap {
  padding: 0 5px;
}

.inputWrapper label img {
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

/* RTL */
*[dir="rtl"] .inputWrapper {
  text-align: right!important;
}

*[dir="rtl"] .inputWrapper input {
  right: 2.5px;
  left: 0;
}

*[dir="rtl"] .inputWrapper span {
  margin-right: 20px;
  margin-left: 0;
}


/***************************************
Xpane Resizer Style
***************************************/
.Resizer {
  background: #000;
  z-index: 1;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  background-clip: padding-box;
  -webkit-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
}

.Resizer.horizontal {
  height: 11px;
  margin: -5px 0;
  border-top: 5px solid rgba(255, 255, 255, 0);
  border-bottom: 5px solid rgba(255, 255, 255, 0);
  cursor: row-resize;
  width: 100%;
}

.Resizer.horizontal:hover {
  border-top: 5px solid rgba(0, 0, 0, 0.1);
  border-bottom: 5px solid rgba(0, 0, 0, 0.1);
}

.Resizer.vertical {
  width: 11px;
  margin: 0 -5px;
  border-left: 5px solid rgba(255, 255, 255, 0);
  border-right: 5px solid rgba(255, 255, 255, 0);
  cursor: col-resize;
}

.Resizer.vertical:hover {
  border-left: 5px solid rgba(0, 0, 0, 0.1);
  border-right: 5px solid rgba(0, 0, 0, 0.1);
}
.Resizer.disabled {
cursor: not-allowed;
}
.Resizer.disabled:hover {
border-color: transparent;
}

/* XPage - RTL */
*[dir="rtl"] .SplitPane {
  direction: ltr;
}

*[dir="rtl"] .SplitPane .Pane {
  direction: rtl;
}

/* the key pressing module add an extra div and it will not show when it focuses*/
div[tabindex="-1"]:focus {
  outline: 0;
}

/* menu z-index override */
.contextMenu .react-contextmenu.react-contextmenu--visible {
  z-index: 103;
}

