  height:100%;
  z-index:100
}

.ui-dialog .ui-dialog-content,
.ui-dialog .ui-dialog-content * {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.ui-dialog{
  position: fixed;
  top: 50%;
  left: 50%;
  outline: 0 none;
  z-index: 101;
  background-color: #fff;
  border: 2px solid #ddd;
  border-width: 2px;
  border-style: outset;
  border-radius: 5px;
  -webkit-box-sizing: content-box;
