}

.cba-dialog .ui-dialog .ui-dialog-titlebar {
  background: #757F9A; /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #757F9A, #828ba4); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, left bottom, from(#757F9A), to(#828ba4));
  background: -webkit-linear-gradient(top, #757F9A, #828ba4);
  background: -o-linear-gradient(top, #757F9A, #828ba4);
  background: linear-gradient(to bottom, #757F9A, #828ba4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.cba-dialog.focused .ui-dialog .ui-dialog-titlebar {
  background-color: rgb(0, 128, 192);
  background: -webkit-gradient(linear, left top, left bottom, from(rgb(0, 95, 172)),to(rgb(0, 80, 146)));
  background: -webkit-linear-gradient(top, rgb(0, 95, 172),rgb(0, 80, 146));
