}

.cba-modal.not-closable .ui-dialog > header, .cba-dialog.not-closable .ui-dialog > header {
  display:none;
}

.cba-modal.not-closable .ui-dialog, .cba-dialog.not-closable .ui-dialog {
  border: 0;
  border-radius: 0;
  border-style: none;
  background: transparent;
}

.cba-dialog.focused .ui-dialog {
  z-index: 98;
}

.ui-dialog.maximized{
  position:fixed;
  top:0;
